.contact-us {
  background-color: var(--theme-primary);
}

.contact-us img {
  width: 60%;
  margin: 30px 0;
}

.contact-us a {
  text-decoration: none;
}

.contact-us p {
  margin: 0;
}

.contact-us h3,
h5 {
  color: white !important;
}

.contact-us a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.815);
  font-size: 17px;
  transition-duration: 0.2s;
  list-style-type: none;
  letter-spacing: 0.015em;
}

.contact-us .address-p-tag {
  color: rgba(255, 255, 255, 0.815);
  font-size: 17px;
  transition-duration: 0.2s;
  list-style-type: none;
  letter-spacing: 0.015em;
}

.contact-us .branch-address {
  margin: 30px 0;
}

.branch-address img {
  width: 130px;
  margin: 20px 0 0 0;
}
